import EvanForm from '@/components/evan-form/evan-form.vue'
import EvanFormItem from '@/components/evan-form/evan-form-item.vue'
// import utils from '@/components/evan-form/utils.js'
import '@/common/utils'
export default {
	components: {
		EvanForm,
		EvanFormItem
	},
	data() {
		return {
			payType: 1,
			orderInfo: {},
			hideRequiredAsterisk: false,
			customerEdit: true,
			carInfoEdit: false,
			carTypeIndex: -1,
			carType: ['轿车', 'SUV', '客车'],
			date: '2018-12-25',
			multiArray: [
				['北京', '河北'],
				['北京'],
				// ['猪肉绦虫', '吸血虫']
			],
			objectMultiArray: [
				[{
						id: 0,
						name: '北京'
					},
					{
						id: 1,
						name: '河北'
					}
				],
				[{
						id: 0,
						name: '北京'
					},
					{
						id: 1,
						name: '石家庄'
					},
					{
						id: 2,
						name: '保定'
					},
					{
						id: 3,
						name: '唐山'
					},
					{
						id: 4,
						name: '张家口'
					}
				]
			],
			multiIndex: [0, 0],
			basicsList: [{
				cuIcon: 'usefullfill',
				name: '客户信息'
			}, {
				cuIcon: 'radioboxfill',
				name: '基本信息'
			}, {
				cuIcon: 'roundclosefill',
				name: '车源图片'
			}, {
				cuIcon: 'roundcheckfill',
				name: '完成'
			}, ],
			basics: 0,
			// 表单的内容必须初始化
			customer: {
				name: 'wcg',
				phone: '13731080174',
			},
			carinfo: {
				carname: '',
				type: '',
			},
			rules: {
				name: {
					required: true,
					message: '请输入姓名'
				},
				phone: [{
						required: true,
						message: '请输入手机号'
					},
					/* {
						validator: (rule, value, callback) => {
							// 注意这里如果用的是methods里的isMobilePhone将不生效
							if (this.$utils.isMobilePhone(value)) {
								callback()
							} else {
								callback(new Error('手机号格式不正确'))
							}
						}
					}, */
					// 或者这样也是可以的
					{
						validator: this.isMobile
					}
				],
			}
		};
	},
	mounted() {
		// 这里必须放在mounted中，不然h5，支付宝小程序等会找不到this.$refs.form
		this.$refs.customerform.setRules(this.rules)
		console.log(Math.pow(1.1, 9))
	},
	computed: {

	},
	onLoad(options) {

	},

	methods: {
		BasicsSteps() {
			this.basics = this.basics == this.basicsList.length - 1 ? 0 : this.basics + 1
		},
		scanVin() {
			console.log('gan jin sao')
		},
		PickerChange(e) {
			this.carTypeIndex = e.detail.value
		},
		MultiChange(e) {
			this.multiIndex = e.detail.value
		},
		MultiColumnChange(e) {
			
			let data = {
				multiArray: this.multiArray,
				multiIndex: this.multiIndex
			};
			const column = e.detail.column;
			const value = e.detail.value;
			/* console.log('哥,你滚了')
			console.log('column',column)
			console.log('value',value) */
			data.multiIndex[column] = value;
			switch (column) {
				case 0:
					switch (data.multiIndex[0]) {
						case 0:
							// console.log('你选了首都')
							this.multiArray[1] = ['北京'];
							break;
						case 1:
							// console.log('你选了河北省')
							this.multiArray[1] = ['石家庄', '保定', '唐山', '张家口'];
							break;
					}
					this.multiIndex.splice(1, 0)
					// console.log('你咋不选')
					break;
			}
			this.multiArray = data.multiArray;
			this.multiIndex = data.multiIndex;
		},
		DateChange(e) {
			this.date = e.detail.value
		},
		confirm() {
			this.$refs.customerform.validate((res) => {
				if (res) {
					uni.showToast({
						title: '验证通过',
					})
					this.customerEdit = false
					this.carInfoEdit = true
					this.basics = 1
				}
			})
		},
		isMobile(rule, value, callback) {
			if (this.$utils.isMobilePhone(value)) {
				callback()
			} else {
				callback(new Error('手机号格式不正确'))
			}
		},
	}
}
